import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router'

@Component({
  selector: 'app-reponer',
  templateUrl: './reponer.component.html',
  styleUrls: ['./reponer.component.css']
})
export class ReponerComponent implements OnInit {

  constructor(private activatedroute: ActivatedRoute) {}

  today2: any;
  nombre: any;

  ngOnInit(){
    this.nombre= this.activatedroute.snapshot.params.nombre;
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();
    this.today2 = dd + '/' + mm + '/' + yyyy;
  }

  guardar(){}

}
